@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.menu-container {
  display: flex;
  flex-direction: row;
}

/* navgation */

.nav-container {
  padding-top: 35px;
  padding-left: 62px;
  text-decoration: none;
}

.nav-button {
  background-color: #474747;
  color: #fff;
  border: none;
  width: 45px;
  height: 45px;
  cursor: pointer;
  display: none;
}

.nav-button-icons {
  width: 45px;
  height: 45px;
}

.projects-link {
  font-size: 30px;
  font-family: "Roboto", sans-serif;
  color: #fff;
  text-decoration: none;
}

.projects-link:hover {
  color: rgb(161, 161, 161);
  transition: 0.3s;
}

.contact-link {
  background-color: #474747;
  margin-left: 43px;
  color: #fff;
  border: none;
  text-decoration: none;
  font-size: 30px;
  font-family: "Roboto", sans-serif;
}

.contact-link:hover {
  color: rgb(161, 161, 161);
  transition: 0.3s;
}

/* logo */

.logo-container {
  margin-right: auto;
  margin-left: auto;
}

.logo-img {
  margin-top: -30px;
  width: 180px;
  height: 180px;
}

.logo-img-reduced {
  margin-top: 22px;
  margin-left: 45%;
  width: 90px;
  height: 90px;
  display: none;
}

/* social */

.social-container {
  padding-top: 35px;
  padding-right: 62px;
  height: 45px;
}

.social-buttons {
  margin-left: 53px;
  background-color: #474747;
  border: none;
  color: #fff;
  width: 45px;
  height: 45px;
}

.social-buttons:hover {
  transition: 0.3s;
  color: rgb(161, 161, 161);
}

.social-icons {
  width: 45px;
  height: 45px;
}

/* side menu */

.side-menu {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #240b53;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  width: 60%;
  height: 100%;
}

.side-menu-link {
  margin: 20px;
  color: #fff;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-size: 25px;
}

.side-menu-link:hover {
  color: rgb(126, 126, 126);
  transition: 0.3s;
}

.close-button-menu {
  background-color: #240b53;
  border: none;
  margin-left: 90%;
  margin-top: 8px;
}

.close-icon-menu {
  color: #fff;
  width: 23px;
  height: 23px;
}

.close-icon-menu:hover {
  color: rgb(126, 126, 126);
  transition: 0.3s;
}

/* responsive */

@media (max-width: 980px) {
  .projects-link {
    display: none;
  }
  .contact-link {
    display: none;
  }
  .nav-button {
    display: inline;
    display: initial;
  }
}

@media (max-width: 770px) {
  .logo-img {
    display: none;
  }

  .logo-img-reduced {
    display: inline;
    display: initial;
  }
}

@media (max-width: 630px) {
  .logo-img-reduced {
    display: none;
  }
}

@media (max-width: 490px) {
  .social-container {
    display: none;
  }

  .logo-img {
    margin-top: -17px;
    display: inline;
    display: initial;
    width: 150px;
    height: 150px;
  }

  .nav-container {
    padding-left: 30px;
  }

  .close-button-menu {
    margin-left: 80%;
  }
}

.content {
  display: flex;
  flex-direction: row;
}

/* PERFIL */

.perfil-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.img-frame {
  background-color: #3f3d3d;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  width: 325px;
  height: 311px;
}

.perfil-img {
  border-radius: 50px;
  width: 300px;
  height: 291px;
}

.perfil-name {
  color: #fff;
  font-family: "Josefin Sans", sans-serif;
  font-family: 45px;
  font-weight: 700;
}

.perfil-description {
  color: #fff;
  font-family: "Josefin Sans", sans-serif;
  font-size: 35px;
  text-align: center;
}

/* PRODUCT */

.product-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.product-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #3f3d3d;
  border-radius: 25px;
  width: 480px;
  height: 470px;
}

.tech-products {
  color: #fff;
  margin-top: 42px;
  font-family: "Josefin Sans", sans-serif;
  font-family: 45px;
  font-weight: 700;
}

.tech-icons {
  width: 65px;
  height: 65px;
  padding: 20px;
}

#JS {
  color: #f7df1e;
}

#React {
  color: #00d8ff;
}

#HTML {
  color: #e34c26;
}

.skills-list {
  color: #fff;
  font-family: "Josefin Sans", sans-serif;
  font-size: 30px;
  font-style: italic;
  font-weight: 500;
}

/* RESPONSIVE */

@media (max-width: 980px) {
  .content {
    flex-direction: column;
  }
  .perfil-container {
    margin-top: 15px;
  }
  .product-container {
    margin-top: 15px;
    margin-bottom: 30px;
  }
}

@media (max-width: 980px) {
  .perfil-container {
    margin-top: 50px;
  }
  .product-container {
    margin-top: 30px;
  }
}

@media (max-width: 550px) {
  .img-frame {
    width: 305px;
    height: 291px;
  }

  .perfil-img {
    width: 280px;
    height: 271px;
  }

  .perfil-description {
    font-size: 25px;
  }

  .product-box {
    width: 410px;
    height: 400px;
  }

  .skills-list {
    font-size: 22px;
  }
}

@media (max-width: 450px) {
  .product-box {
    border-radius: 0px;
    width: 100%;
  }
}

.home-icon {
  margin: 10px;
  color: #fff;
  width: 50px;
  height: 50px;
}

.home-icon:hover {
  color: rgb(161, 161, 161);
  transition: 0.3s;
}

.form {
  margin: 25px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  width: 450px;
  height: 500px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}

._img {
  width: 400px;
  height: 400px;
}

.form-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  background-color: #000;
  width: 450px;
  height: 500px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}

.form-title {
  color: #fff;
  font-family: "Roboto", sans-serif;
}

.inp {
  padding: 15px;
  margin: 20px;
  width: 300px;
  height: 20px;
  border: solid;
  border-color: #5200ff;
  border-radius: 15px;
}

.message {
  margin-top: 22px;
  margin-bottom: 2px;
  color: #fff;
  font-family: "Roboto", sans-serif;
}

#txtarea {
  height: 160px;
  margin-bottom: 42px;
}

.submit-container {
  display: flex;
  justify-content: center;
}

.submit-btn {
  color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 25px;
  background-color: #5200ff;
  border: none;
  border-radius: 10px;
  width: 200px;
  height: 50px;
}

.submit-btn:hover {
  cursor: pointer;
  transition: 0.3s;
  background-color: #3b04b3;
  height: 45px;
}

/* RESPONSIVE */

@media (max-width: 825px) {
  .img-container {
    display: none;
  }

  .form-container {
    border-radius: 10px;
  }
}

@media (max-width: 400px) {
  .form-container {
    width: 300px;
    height: 350px;
  }

  .inp {
    width: 200px;
    height: 100px;
    margin: 5px;
  }
}

@media (max-width: 320px) {
  .form-container {
    background-color: #474747;
  }

  .inp {
    margin: 7px;
    border-color: #000;
  }
}

.container-projects {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#menu_ {
  margin-right: 95%;
}

.img_ {
  width: 200px;
  height: 200px;
}

.product {
  display: flex;
  flex-direction: row;
  margin-bottom: 70px;
}

._content {
  cursor: pointer;
  padding: 10px;
  margin-left: 50px;
  color: #fff;
  background-color: #474747;
  border: none;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  width: 650px;
}

._content:hover {
  transition: 0.3s;
  background-color: rgba(0, 0, 0, 0.24);
  border-radius: 10px;
}

.pTitle {
  font-size: 55px;
  font-weight: 700;
}

.description {
  font-size: 25px;
  font-weight: 700;
  margin-top: -30px;
}

.link-txt {
  color: #fff;
  font-family: "Roboto", sans-serif;
  text-align: center;
  font-size: 50px;
}

.link-txt:hover {
  transition: 0.3s;
  color: rgb(163, 163, 163);
}

.link-together {
  text-decoration: none;
}

.link-div {
  margin-top: 30px;
  margin-bottom: 100px;
}

@media (max-width: 1150px) {
  ._content {
    width: 500px;
  }
}

@media (max-width: 950px) {
  .img_ {
    margin-top: 45px;
    width: 130px;
    height: 130px;
  }

  .link-txt {
    font-size: 40px;
  }
}

@media (max-width: 800px) {
  .pTitle {
    font-size: 40px;
  }

  .description {
    font-size: 20px;
  }

  .product {
    margin-left: 55px;
  }

  #menu_ {
    margin-right: 90%;
  }
}

@media (max-width: 750px) {
  .product {
    flex-direction: column;
    align-items: center;
    margin-left: -40px;
  }

  .img_ {
    margin-left: 50px;
    margin-bottom: 20px;
  }

  .link-txt {
    font-size: 30px;
  }
}

@media (max-width: 520px) {
  .link-txt {
    font-size: 20px;
  }

  .description {
    font-size: 15px;
  }

  .pTitle {
    font-size: 30px;
    margin-bottom: 40px;
  }

  .img_ {
    width: 100px;
    height: 100px;
  }

  ._content {
    width: 350px;
    background-color: rgb(56, 56, 56);
    border-radius: 20px;
  }
}

@media (max-width: 360px) {
  ._content {
    width: 250px;
  }

  #menu_ {
    margin-right: 85%;
  }

  .link-txt {
    font-size: 15px;
  }
}

